.nav-primary {
  border-bottom: 4px solid @msu-gold;
  
  &.navbar-default {
    background: none !important;
  }
  
  .logo-btn {
    background-color: @white;
    padding: @space-md;
    
    &:focus,
    &:hover {
      background-color: @white;
    }
    
    img {
      display: block;
      margin: 0 auto;
    }
    
  }
  
  .btn-primary,
  .btn-secondary {
    
    a {
      font-size: 1.1em;
      padding: 0.75em @space-md;
    
      &:focus,
      &:hover {
        background-color: @msu-gold;
      }
      
    }
    
  }
  
  .btn {
    background: @black;
    padding: 0.5em;
  }
  
  a {
    color: @white;
    display: block; 
  }

  ul {
    margin-bottom: 0;
  }
  
  li {
    margin-bottom: 1px;
  }
  
}